.intro {
    height: 100vh;
    position: relative;
    color: #fff;
}

.intro .swiper-pagination {
    bottom: 50px;
    z-index: 100000;
}

.intro .swiper-pagination .swiper-pagination-bullet {
    width: 18px;
    height: 10px;
    background-color: #fff;
    border-radius: 0;
    opacity: 1;
    transition: .3s;
}

.intro .swiper-pagination .swiper-pagination-bullet-active {
    background-color: #c48d17 !important;
    width: 36px;
}

.intro .intro-item {
    padding-top: 2rem;
}

.intro .carousel-item-a {
    position: relative;
    height: 100vh;
}

.intro .carousel-item-a.bg-image {
    background-attachment: scroll;
}

.intro .intro-content {
    position: absolute;
}

.intro .intro-body {
    padding-left: 1rem;
}

@media (min-width: 768px) {
    .intro .intro-body {
        padding-left: 2rem;
    }
}

.intro .intro-body .price-a {
    color: #fff;
    padding: 1rem 1.5rem;
    border: 2px solid #c48d17;
    border-radius: 50px;
    text-transform: uppercase;
    letter-spacing: .03em;
}

.intro .spacial {
    -webkit-animation-delay: .2s;
    animation-delay: .2s;
    color: #fff;
}

.intro .intro-title-top {
    margin-bottom: 1rem;
    font-size: 0.9rem;
    color: #fff;
    letter-spacing: .05em;
}

@media (min-width: 768px) {
    .intro .intro-title-top {
        font-size: 0.9rem;
        margin-bottom: 1rem;
    }
}

@media (min-width: 992px) {
    .inro .intro-title-top {
        font-size: 1rem;
        margin-bottom: 2rem;
    }
}

.intro .intro-title {
    color: #fff;
    font-weight: 700;
    font-weight: 2.8rem;
    text-transform: uppercase;
}

@media (min-width: 576px) {
    .intro .intro-title {
        font-size: 3.5rem;
    }
}

@media (min-width: 768px) {
    .intro .intro-title {
        font-size: 3.5rem;
    }
}

@media (min-width: 992px) {
    .intro .intro-title {
        font-size: 4rem;
    }
}

.intro .intro-subtitle {
    font-size: 1.5rem;
    font-weight: 300;
}

@media (min-width: 768px) {
    .intro .intro-subtitle {
        font-size: 2.5rem;
    }
}

.intro .intro-subtitle.intro-price {
    padding-top: 1rem;
    font-size: 1.2rem;
}

@media (min-width: 576px) {
    .intro .intro-subtitle.intro-price {
        font-size: 1.3rem;
    }
}

@media (min-width: 768px) {
    .intro .intro-subtitle.intro-price {
        padding-top: 2rem;
        font-size: 1.5rem;
    }
}

@media (min-width: 992px) {
    .intro .intro-subtitle.intro-price {
        padding-top: 3rem;
    }
}

.intro-single {
    padding: 12rem 0 3rem;
}

@media (max-width: 767px) {
    .intro-single {
        padding-top: 9rem;
    }
}

.intro-single .title-single-box {
    padding: 1rem 0 1rem 2rem;
    border-left: 3px solid #c48d17;
}

.intro-single .title-single-box .title-single {
    font-weight: 600;
    font-size: 2.2rem;
}

@media (min-width: 768px) {
    .intro-single .title-single-box .title-single {
        font-size: 2.1rem;
    }
}

@media (min-width: 992px) {
    .intro-single .title-single-box .title-single {
        font-size: 2.5rem;
    }
}

.intro-single .breadcrumb-box {
    padding: 1rem 0 0 0.5rem;
}

.intro-single .breadcrumb {
    background-color: transparent;
    padding-right: 0;
    padding-left: 0;
}







